___CSS_LOADER_EXPORT___.push([module.id, `#container {
  width: 100vw;
}

h2, h5 {
  text-align: center;
  margin: 10px 0px;
}

.swiper-container {
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  overflow-y: auto;
  flex-wrap: nowrap;
  width: 100%;
  height: 80vh;
  margin-top: 10vh;
}

.navigator {
  height: 10vh;
  z-index: 2;
  position: absolute !important;
}

.navigator .btn-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  bottom: 10px;
  width: 100%;
  height: 10vh;
  transition: all 0.3s linear;
}

.navigator .btn-ele {
  padding: 5px;
  margin: 10px;
  text-decoration: none;
  color: #000;
  background-color: #d4d4d4;
  border-radius: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: auto;
  height: 50%;
  transition: all 0.3s linear;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}

/* cart icon should be larger than usually to catch customer attention */
.navigator .btn-ele.cart {
  padding: 10px;
  height: 70%;
}

.navigator .btn-ele .img {
  height: 100%;
  aspect-ratio: 1;
}

.navigator .btn-ele img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.navigator .btn-ele > * {
  margin-right: 5px;
}

.heading {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.template-wrapper {
  background-color: #fff;
  width: 100%;
  height: 100% !important;
}

.notHaveTemplate {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 100px;
}

.notHaveTemplate > p {
  text-align: center;
}

/* cart style overwritting */
.logo {
  z-index: 2;
  width: 100%;
}

.cart .count {
  top: -7px;
}

.cart_parent .cart_item-img {
  border-radius: 40px;
  background-color: #fff;
}

.logo .cart_parent .cart_item-img > img {
  border-radius: 40px !important;
}

.logo .cart_parent .cart_checkout {
  max-width: 240px;
  cursor: pointer;
}

.logo .cart_parent .cart_items {
  width: auto !important;
  height: auto !important;
  aspect-ratio: 9/16 !important;
  padding: 0px !important;
}

.logo .cart_parent .cart_item {
  height: calc(100% - 50px);
  position: relative;
}

.logo .cart_parent .cart_item-remove {
  text-align: center;
  position: absolute;
  top: -15px;
  left: -15px;
}

.logo .cart_parent .cart_items .cart_empty_msg {
  padding: 40px;
}

/* Web responsive */
@media screen and (max-width: 600px) {
  .heading h1 {
    font-size: 20px;
  }
  .signupParent {
    height: 68vh;
  }
  .signupChild {
    height: 100%;
  }
  .signupChild h1 {
    margin-bottom: 20px;
  }
}
@media screen and (min-width: 600px) {
  .navigator .btn-box {
    top: 10px;
  }
  .navigator .btn-box {
    justify-content: end;
  }
}`, "",{"version":3,"sources":["webpack://./controllers/client/css/template.css"],"names":[],"mappings":"AAAA;EACI,YAAA;AACJ;;AACA;EACI,kBAAA;EACA,gBAAA;AAEJ;;AAAA;EACI,aAAA;EACA,sBAAA;EACA,kBAAA;EACA,gBAAA;EACA,iBAAA;EACA,WAAA;EACA,YAAA;EACA,gBAAA;AAGJ;;AADA;EACI,YAAA;EACA,UAAA;EACA,6BAAA;AAIJ;;AAFA;EACI,aAAA;EACA,8BAAA;EACA,mBAAA;EACA,eAAA;EACA,YAAA;EACA,WAAA;EACA,YAAA;EACA,2BAAA;AAKJ;;AAHA;EACI,YAAA;EACA,YAAA;EACA,qBAAA;EACA,WAAA;EACA,yBAAA;EACA,mBAAA;EACA,aAAA;EACA,mBAAA;EACA,uBAAA;EACA,mBAAA;EACA,WAAA;EACA,WAAA;EACA,2BAAA;EACA,yFAAA;AAMJ;;AAJA,wEAAA;AACA;EACI,aAAA;EACA,WAAA;AAOJ;;AALA;EACI,YAAA;EACA,eAAA;AAQJ;;AANA;EACI,WAAA;EACA,YAAA;EACA,kBAAA;AASJ;;AAPA;EACI,iBAAA;AAUJ;;AAPA;EACI,aAAA;EACA,uBAAA;EACA,mBAAA;EACA,mBAAA;AAUJ;;AARA;EACI,sBAAA;EACA,WAAA;EACA,uBAAA;AAWJ;;AATA;EACI,aAAA;EACA,uBAAA;EACA,mBAAA;EACA,cAAA;AAYJ;;AAVA;EACI,kBAAA;AAaJ;;AAXA,4BAAA;AACA;EACI,UAAA;EACA,WAAA;AAcJ;;AAZA;EACI,SAAA;AAeJ;;AAbA;EACI,mBAAA;EACA,sBAAA;AAgBJ;;AAdA;EACI,8BAAA;AAiBJ;;AAfA;EACI,gBAAA;EACA,eAAA;AAkBJ;;AAhBA;EACI,sBAAA;EACA,uBAAA;EACA,6BAAA;EACA,uBAAA;AAmBJ;;AAjBA;EACI,yBAAA;EACA,kBAAA;AAoBJ;;AAlBA;EACI,kBAAA;EACA,kBAAA;EACA,UAAA;EACA,WAAA;AAqBJ;;AAnBA;EACI,aAAA;AAsBJ;;AApBA,mBAAA;AACA;EACI;IACI,eAAA;EAuBN;EArBE;IACI,YAAA;EAuBN;EArBE;IACI,YAAA;EAuBN;EArBE;IACI,mBAAA;EAuBN;AACF;AApBA;EACI;IACI,SAAA;EAsBN;EApBE;IACI,oBAAA;EAsBN;AACF","sourcesContent":["#container {\n    width: 100vw;\n}\nh2, h5 {\n    text-align: center;\n    margin: 10px 0px;\n}\n.swiper-container {\n    display: flex;\n    flex-direction: column;\n    overflow-x: hidden;\n    overflow-y: auto;\n    flex-wrap: nowrap;\n    width: 100%;\n    height: 80vh;\n    margin-top: 10vh;\n}\n.navigator {\n    height: 10vh;\n    z-index: 2;\n    position: absolute !important;\n}\n.navigator .btn-box {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    position: fixed;\n    bottom: 10px;\n    width: 100%;\n    height: 10vh;\n    transition: all .3s linear;\n}\n.navigator .btn-ele {\